:root {
  --background-rgb: 18, 18, 24;
  --body-rgb:       239, 239, 239; }

@media only screen and (max-width: 700px) {
  .pageWrapper {
    padding: 12px; } }

@media only screen and (min-width: 700px) {
  .pageWrapper {
    padding: 1in; } }

header {
  display: flex;
  align-items: center;
  justify-content: space-between; }
  header .left {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: -22px;
    padding: 11px; }
    header .left .navLink {
      margin: 11px; }
    header .left .navLink, header .left .navLink:link, header .left .navLink:visited {
      font-size: 2rem;
      color: rgb(var(--body-rgb));
      text-decoration: initial;
      font-style: italic;
      font-weight: 700;
      transition: font-size 100ms ease;
      height: 2.5rem;
      vertical-align: middle; }
      @media only screen and (max-width: 700px) {
        header .left .navLink, header .left .navLink:link, header .left .navLink:visited {
          font-size: 1.5rem; } }
    header .left .navLink:hover {
      color: orange;
      font-size: 2.5em; }

h1 {
  font-size: 1.4em;
  font-style: italic;
  font-weight: bold; }

h3 {
  font-size: 1em;
  font-style: italic;
  font-weight: initial; }

h1 + h3 {
  margin-top: -8px; }

hr {
  color: rgba(var(--body-rgb), 0.3); }
