:root {
  --background-rgb: 18, 18, 24;
  --body-rgb:       239, 239, 239; }

.container {
  display: flex;
  flex-direction: column; }

@keyframes spin {
  0% {
    transform: scaleX(0); }
  50% {
    transform: scaleX(1); }
  100% {
    transform: scaleX(0); } }

.loader {
  display: flex;
  align-items: center;
  justify-content: center; }
  .loader .inner {
    border: 4px solid rgb(var(--body-rgb));
    height: 26px;
    width: 26px;
    border-radius: 50%;
    animation-name: spin;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-out; }

.post {
  width: 100%; }
